@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
#modal-window {
  position: absolute;
  width: 99%;
  top: 0;
  z-index: 99999;
  font-family: Inter;
  background-color: '#000';
}

.modal-window {
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  will-change: transform;
  margin-top: 2em;
  position: fixed;
  background: #fff;
  width: 500px;  
  height: 300px;
  box-shadow: 0 .5rem 1rem rgba(0,0,0,.15) !important;
  z-index: 9999;
  border-radius: 10px;
  border: 0;
  color: #000;
}

.modal-window-inner-border {
  min-height: 200px;
  height: auto;
  height: initial;
  display: flex;
  box-shadow: 0 0 0 0.4px #222132;
}

.modal-window-inner-border:before {
  position: absolute;
  top: .4px;
  left: .4px;
  right: .4px;
  bottom: .4px;
  border: .5px solid #57566d;
}

.modal-handle {
  cursor: move;
  height: 15px;
  border-radius: 3px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.modal-content {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: left;
  color: white;
  font-size: 14px;
  line-height: 24px;
}

.modal-content-drawer-handle {
  height: 100%;
  width: 20px;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-close-button {
  cursor: pointer;
}

.modal-body {
  display: flex;
  flex-direction: column;
}

.modal-button {
  box-sizing: border-box;
  outline: 0;
  border: 0;
  height: 48px;
  font-size: 14px;
  transition: 200ms ease all;
  overflow-wrap: break-word;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
  background-color: #fff;
  color: #8E8E93;
  bottom: 0;
  text-align: left;
  padding-left: 40px;
  font-size: 16px;
}

.modal-button:hover {
  background-color: #EAEAEE;
  color: #8E8E93;
}

.modal-button-main {
  outline: 0;
  border: 0;
  height: 48px;
  line-height: 48px;
  font-size: 14px;
  transition: 200ms ease all;
  overflow-wrap: break-word;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
  background-color: #fff;
  color: #8E8E93;
  bottom: 0;
  text-align: left;
  font-size: 16px;
  display: flex;
  position: relative;
}

.modal-button-main:hover {
  background-color: #EAEAEE;
  color: #8E8E93;
}

.slate-button-selected {
  background-color: '#4d4' !important;
}

.modal-search-bar {
  position: relative;
  background-color: #F2F2F7;
  top: 0;
  left: 0;
  width: 100%;
  border-radius: 10px 10px 0 0;
}

.modal-search-input {
  background-color: #F2F2F7;
  top: 0;
  left: 0;
  width: 300px;
  border: 0;
  outline:none;
  font-size: 14px;
  height: 50px;
  color: #8E8E93;
  border-radius: 10px 10px 0 0;
  margin-left: 40px;
  font-family: Inter;
}

.metadata {
  color: #48484A;
  display: flex;
  height: 86px;
  justify-content: center;
  align-items: center;
  margin: 12px 0 12px 0;
}

.metadata-box {
  flex: 1 1;
  font-size: 16px;
}

.metadata-image {
  border-radius: 4px;
  margin-left: 16px;
  object-fit: cover;
}

.metadata-box2 {
  flex: 1 1;
